/**
 *
 * config  Object
 * config.url 
 * config.intervalMsgInfo 
 * config.intervalMsgInfo.msgs   Array
 * config.intervalMsgInfo.timeout  Number
 * config.onmessage 
 * 
 * 
    import { socket } from "@/utils/socket.js";
    data() {
      return{
        socket: null
      }
    }

    getSocket(msgs, callback) {
      const config = {
        url: "/socket/websocket",
        intervalMsgInfo: {
          msgs: msgs, //  
          timeout: 10 * 1000
        },
        onmessage: evt => {
          const msgData = JSON.parse(evt.data);
          callback(msgData);
        }
      };
      this.socket = socket(config);
    }

    // socket
    this.socket.send(JSON.stringify(msg)); msg  
    
    // socket
    this.socket.close();
 *
 */
export default class Socket {
  constructor(config) {
    this.config = config
    // ws
    this.ws = null
    // 
    this.timer = null
    //  true
    this.lockReconnect = false
    // 
    this.reconnectCount = 0
    // ws
    this.receiveFun = null
    // wss 
    this.socketUrl = `${(window.location.hostname === "www.wdh.pw" || window.location.hostname === "localhost") ? "ws" : "ws"}://${window.location.host || process.env.VUE_APP_BASE_BASE_HTTP_URL.replace(/http:\/\/|https:\/\//g, '')}${config.url}`
    this.init()
    // return this.init()
  }

  // 
  // apicloudws window.apiapp
  // 2
  init() {
    if (window.api) {
      return this.initAppWS()
    } else {
      return this.initWebWS()
    }
  }

  initWebWS(callback) {
    try {
      if ('WebSocket' in window) {
        this.ws = new WebSocket(this.socketUrl)

        // eslint-disable-next-line no-unused-vars
        this.ws.onopen = function (evt) {
          // startTimerMsg() // 
          console.log("Connection open ...")
          if (callback) callback()
        }

        this.ws.onmessage = (evt) => {
          // 
          const msgData = JSON.parse(evt.data);
          if (this.receiveFun) this.receiveFun(msgData)
        }

        this.ws.onerror = () => {
          if (this.reconnectCount > 5) {
            console.log(`5`)
            return
          }
          this.reconnect(this.socketUrl)
          this.reconnectCount++
          console.log(`ws!${this.reconnectCount}`)
        }

        // eslint-disable-next-line no-unused-vars
        this.ws.onclose = (evt) => {
          if (this.timer) clearInterval(this.timer)
          console.log("Connection closed." + new Date().toLocaleString())
        }
        return this.ws
      }
    } catch (e) {
      console.log('err')
      this.reconnect(this.socketUrl)
      console.log(e)
    }
  }
  // apiCloudws
  initAppWS(callback) {
    const self = this
    this.ws = window.api.require('myWebSocket');
    return new Promise((resolve) => {
      this.ws.open({
        url: this.socketUrl,
        pingInterval: 30,
        pingData: 'ping',
        autoReconnect: false //
      }, function (ret) {
        console.log('ret ======= ' + JSON.stringify(ret));
        if (ret) {// eslint-disable-next-line no-unused-vars
          self.ws.bindEvent(function (ret, err) {
            var type = ret.type;
            switch (type) {
              case 'opened':
                //...
                // startTimerMsg() // 
                console.log("Connection open ...")
                resolve(self.ws)
                break;
              case 'receive':
                // self.config.onmessage(ret)
                if (self.receiveFun) self.receiveFun(JSON.parse(ret.data))
                break;
              case 'error':
                //do something ...
                if (self.reconnectCount > 5) {
                  console.log(`5`)
                  return
                }
                self.reconnect(self.socketUrl)
                self.reconnectCount++
                console.log(`self.!${self.reconnectCount}`)
                break;
              case 'closed':
                //do something ...
                console.log("Connection closed." + new Date().toLocaleString())
                if (self.timer) clearInterval(self.timer)
                break;
            }
          });
          if (callback) callback()
        } else {
          console.log('websocket');
        }
      });
    })
  }

  // 
  setReceiveFun(func) {
    this.receiveFun = func
  }

  reconnect() {
    if (this.lockReconnect) return
    this.lockReconnect = true
    setTimeout(function () { // 
      if (window.api) {
        this.initAppWS(this.socketUrl)
      } else {
        this.initWebWS(this.socketUrl)
      }
      this.lockReconnect = false
    }, 1000)
  }

  // 
  sendMsg(msg) {
    if (window.api) {
      // 
      if (this.ws.readyState() !== 1) {
        setTimeout(() => {
          this.initAppWS(this.sendMsg(msg))
        }, 1000)
        return
      }
    } else {
      if (this.ws.readyState !== 1) {
        setTimeout(() => {
          this.initWebWS(this.sendMsg(msg))
        }, 1000)
        return
      }
    }
    if (msg instanceof Array) {
      this.toSendMsg(msg)
    } else {
      this.startTimerMsg(msg)
    }
  }
  // eslint-disable-next-line no-unused-vars
  /**
   * 
   * @param {Object} msgsObj
   * @param {Array} msgsObj.msgs 
   * @param {Number} msgsObj.timeout 
   */
  startTimerMsg(msgsObj) {
    const msgs = msgsObj.msgs
    this.toSendMsg(msgs)
    //  
    this.timer = setInterval(() => {
      this.toSendMsg(msgs)
    }, msgsObj.timeout)
  }

  /**
   * 
   * @param {Array} msgs 
   */
  toSendMsg(msgs) {
    let cancel = false
    if (window.api) {
      // if (this.ws.readyState() !== 1) { return }
      msgs.forEach(msg => {
        if (msg.key.includes('cancel')) {
          cancel = true
        }
        const re = this.ws.send({ msg: JSON.stringify(msg) })
        console.log(re);
      })
    } else {
      // if (this.ws.readyState !== 1) return
      msgs.forEach(msg => {
        if (msg.key.includes('cancel')) {
          cancel = true
        }
        this.ws.send(JSON.stringify(msg))
      })
    }
    if (cancel) {
      clearInterval(this.timer)
      this.timer = null
    }
  }
}
