import Vue from 'vue'
import keys from '@/config/keyValue'

/**
 * @description:  https://cn.vuejs.org/v2/guide/filters.html
 * @param {string} k  
 * @param {string} filterKey  a.b.c
 * @return {string}
 * 
 * 1 -->{{ message | capitalize }} ;  `v-bind`  --> <div :id="rawId | formatId"></div>      '0' | KeyFilter('status')
 * 2 KeyFilter(i18n.locale,'common.langNum')
 */
export function KeyFilter(k, filterKey) {
  // 
  const filterKeyArr = filterKey.split('.')
  let targetArr = keys
  filterKeyArr.map((item) => {
    targetArr = targetArr[item]
  })
  const data = targetArr.find((item) => item.key === k.toString())
  return data ? data.value : k
}

Vue.filter('KeyFilter', KeyFilter)
